/// Overall Layout structure
* {
  margin: 0;
  padding: 0;
}
html {
  font-size: 62.5%; /* 1 rem is now 1 10 pixles*/
  box-sizing: border-box;
}

// Mobile
@media (max-width: 530px) {
  .App {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;

    display: grid;
    grid-template-rows: 65px 185vw auto auto auto auto 180vw;

    grid-template-areas:
      "nav"
      "section-one"
      "section-two"
      "section-three"
      "section-four"
      "section-five"
      "section-six";
  }

  .nav-area {
    grid-area: nav;
    background-color: #000000;
  }
  .section-one {
    grid-area: section-one;
    background-color: #ffffff;
  }
  .section-two {
    grid-area: section-two;
    background-color: rgba(255, 192, 203, 0.69);
    margin-bottom: 10vw;
  }
  .section-three {
    grid-area: section-three;
    background-color: #d8d3f8;
    margin-bottom: 10vw;
  }
  .section-four {
    grid-area: section-four;
    background-color: #dbefc2;
  }
  .section-five {
    grid-area: section-five;
    background-color: #ffffff;
  }
  .section-six {
    grid-area: section-six;
    background-color: #000000;
  }
}

@media (min-width: 531px) {
  .App {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;

    display: grid;
    grid-template-rows: auto auto auto auto auto auto;

    grid-template-areas:
      "nav"
      "section-one"
      "section-two"
      "section-three"
      "section-four"
      "section-five"
      "section-six";
  }

  .nav-area {
    grid-area: nav;
    background-color: #000000;
  }
  .section-one {
    grid-area: section-one;
    background-color: #ffffff;
  }
  .section-two {
    grid-area: section-two;
    background-color: #ffc0cbb0;
    //margin-bottom: 10vw;
    // margin: 0 7vw;
    margin: 0 17vw;
  }
  // .section-three {
  //   grid-area: section-three;
  //   background-color: #d8d3f8;
  //   margin-bottom: 10vw;
  // }
  .section-three {
    grid-area: section-three;
    background-color: #d8d3f8;
    margin: 6vw 17vw;
  }
  // .section-four {
  //   grid-area: section-four;
  //   background-color: #dbefc2;
  // }
  .section-four {
    grid-area: section-four;
    background-color: #dbefc2;
    margin: 0vw 17vw;
  }
  // .section-five {
  //   grid-area: section-five;
  //   background-color: #ffffff;
  // }
  .section-five {
    grid-area: section-five;
    background-color: #ffffff;
    margin: 0vw 17vw;
  }
  .section-six {
    grid-area: section-six;
    background-color: #000000;
  }
}

/// Imports from components
@import "src/components/navigation/navigation.scss";
@import "src/components/sectionOne/One.scss";
@import "src/components/sectionTwo/Two.scss";
@import "src/components/sectionThree/Three.scss";
@import "src/components/sectionFour/Four.scss";
@import "src/components/sectionFive/Five.scss";
@import "src/components/sectionSix/Six.scss";
